import { message } from "antd";

import { useEffect, useState } from "react";

import { GetCorporateCustomerHeaders } from "./columns";
import { corporateCustomerApi } from "../../api/CorporateCustomer";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { CorporateCustomer } from "../../interfaces";

export const CorporateCustomers = () => {
  const [corporateCustomers, setCorporateCustomers] = useState<
    CorporateCustomer[]
  >([]);
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    setLoading(true);
    corporateCustomerApi
      .getCorporateCustomers()
      .then((data) => {
        if (data) {
          setCorporateCustomers(data);
        }
      })
      .finally(() => setLoading(false));
  }, []);

  const onAdd = () => {
    corporateCustomerApi
      .createCorporateCustomer()
      .then((newCorporateCustomer) => {
        if (newCorporateCustomer) {
          setCorporateCustomers([...corporateCustomers, newCorporateCustomer]);
        }
      });
  };

  const onUpdate = (updatedCorporateCustomer: CorporateCustomer) => {
    corporateCustomerApi
      .updateCorporateCustomer(updatedCorporateCustomer)
      .then(() => {
        const index = corporateCustomers.findIndex(
          (cc) => cc.id === updatedCorporateCustomer.id
        );
        if (index !== -1) {
          const updatedDataSource = [...corporateCustomers];
          updatedDataSource.splice(index, 1, updatedCorporateCustomer);
          setCorporateCustomers(updatedDataSource);
        }
      });
  };

  const onDelete = (id: number) => {
    corporateCustomerApi.deleteCorporateCustomer({ id }).then(() => {
      const index = corporateCustomers.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...corporateCustomers];
        updatedDataSource.splice(index, 1);
        setCorporateCustomers(updatedDataSource);
      }
    });
  };

  const onFileUpload = async ({ file }: { file: File }) => {
    try {
      const data = await corporateCustomerApi.uploadExcelFile(file);
      data && setCorporateCustomers(data);
      message.success(`  : ${file.name}`);
    } catch (error) {
      message.error(`   : ${file.name}`);
    }
  };

  const onFileDownload = async () => {
    corporateCustomerApi
      .downloadExcelFile()
      .then((data: Blob) => {
        const url = window.URL.createObjectURL(new Blob([data]));
        const link = document.createElement("a");
        link.style.display = "none";
        link.href = url;
        link.setAttribute("download", "CorporateCustomers.xlsx");
        document.body.appendChild(link);
        link.click();
        link.remove();
      })
      .catch((error: Error) => {
        console.error(":", error);
        message.error(error.message || "     Excel");
      });
  };

  return (
    <>
      <EditableTable
        headers={GetCorporateCustomerHeaders()}
        data={corporateCustomers}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
        onFileUpload={onFileUpload}
        onFileDownload={onFileDownload}
        loading={loading}
      />
    </>
  );
};
