import { message } from "antd";

import { useEffect, useState } from "react";

import { GetLicenseActionHeaders } from "./columns";
import { licenseActionApi } from "../../api/LicenseAction";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { LicenseAction } from "../../interfaces";

export const LicenseActions = () => {
  const [licenseActions, setLicenseActions] = useState<LicenseAction[]>([]);
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    setLoading(true);
    licenseActionApi
      .getLicenseActions()
      .then((data) => {
        if (data) {
          setLicenseActions(data);
        }
      })
      .finally(() => setLoading(false));
  }, []);

  const onAdd = () => {
    licenseActionApi.createLicenseAction().then((newLicenseAction) => {
      if (newLicenseAction) {
        setLicenseActions([...licenseActions, newLicenseAction]);
      }
    });
  };

  const onUpdate = (updatedLicenseAction: LicenseAction) => {
    licenseActionApi.updateLicenseAction(updatedLicenseAction).then(() => {
      const index = licenseActions.findIndex(
        (cc) => cc.id === updatedLicenseAction.id
      );
      if (index !== -1) {
        const updatedDataSource = [...licenseActions];
        updatedDataSource.splice(index, 1, updatedLicenseAction);
        setLicenseActions(updatedDataSource);
      }
    });
  };

  const onDelete = (id: number) => {
    licenseActionApi.deleteLicenseAction({ id }).then(() => {
      const index = licenseActions.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...licenseActions];
        updatedDataSource.splice(index, 1);
        setLicenseActions(updatedDataSource);
      }
    });
  };

  const onFileUpload = async ({ file }: { file: File }) => {
    try {
      const data = await licenseActionApi.uploadExcelFile(file);
      data && setLicenseActions(data);
      message.success(`  : ${file.name}`);
    } catch (error) {
      message.error(`   : ${file.name}`);
    }
  };

  const onFileDownload = async () => {
    licenseActionApi
      .downloadExcelFile()
      .then((data: Blob) => {
        const url = window.URL.createObjectURL(new Blob([data]));
        const link = document.createElement("a");
        link.style.display = "none";
        link.href = url;
        link.setAttribute("download", "LicenseActions.xlsx");
        document.body.appendChild(link);
        link.click();
        link.remove();
      })
      .catch((error: Error) => {
        console.error(":", error);
        message.error(error.message || "     Excel");
      });
  };

  return (
    <>
      <EditableTable
        headers={GetLicenseActionHeaders()}
        data={licenseActions}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
        onFileUpload={onFileUpload}
        onFileDownload={onFileDownload}
        loading={loading}
      />
    </>
  );
};
