import { SoftwareLifeStageContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { SoftwareLifeStage } from "../../interfaces";

export type { SoftwareLifeStageContext } from "./interfaces";

export const softwareLifeStageApi = {
  getSoftwareLifeStages: async () => {
    try {
      const response = await axiosInstance.get<SoftwareLifeStage[]>(
        "/SoftwareLifeStage/getSoftwareLifeStages"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getSoftwareLifeStage: async (context: SoftwareLifeStageContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<SoftwareLifeStage>(
        `/SoftwareLifeStage/getSoftwareLifeStage/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createSoftwareLifeStage: async () => {
    try {
      const response = await axiosInstance.post<SoftwareLifeStage>(
        "/SoftwareLifeStage/createSoftwareLifeStage"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updateSoftwareLifeStage: async (context: SoftwareLifeStage) => {
    try {
      await axiosInstance.put(
        "/SoftwareLifeStage/updateSoftwareLifeStage",
        context
      );
    } catch (error) {
      console.error(error);
    }
  },
  deleteSoftwareLifeStage: async (context: SoftwareLifeStageContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(
        `/SoftwareLifeStage/deleteSoftwareLifeStage/${id}`
      );
    } catch (error) {
      console.error(error);
    }
  },
  uploadExcelFile: async (file: File) => {
    try {
      const formData = new FormData();
      formData.append("file", file);
      const response = await axiosInstance.post<SoftwareLifeStage[]>(
        "/SoftwareLifeStage/importExcel",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        }
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
      throw error;
    }
  },
  downloadExcelFile: async () => {
    try {
      const response = await axiosInstance.get(
        "/SoftwareLifeStage/exportExcel",
        {
          responseType: "blob"
        }
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
};
