import { message } from "antd";

import { useEffect, useState } from "react";

import { GetMetricHeaders } from "./columns";
import { metricApi } from "../../api/Metric";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { Metric } from "../../interfaces";

export const Metrics = () => {
  const [metrics, setMetrics] = useState<Metric[]>([]);
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    setLoading(true);
    metricApi
      .getMetrics()
      .then((data) => {
        if (data) {
          setMetrics(data);
        }
      })
      .finally(() => setLoading(false));
  }, []);

  const onAdd = () => {
    metricApi.createMetric().then((newMetric) => {
      if (newMetric) {
        setMetrics([...metrics, newMetric]);
      }
    });
  };

  const onUpdate = (updatedMetric: Metric) => {
    metricApi.updateMetric(updatedMetric).then(() => {
      const index = metrics.findIndex((cc) => cc.id === updatedMetric.id);
      if (index !== -1) {
        const updatedDataSource = [...metrics];
        updatedDataSource.splice(index, 1, updatedMetric);
        setMetrics(updatedDataSource);
      }
    });
  };

  const onDelete = (id: number) => {
    metricApi.deleteMetric({ id }).then(() => {
      const index = metrics.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...metrics];
        updatedDataSource.splice(index, 1);
        setMetrics(updatedDataSource);
      }
    });
  };

  const onFileUpload = async ({ file }: { file: File }) => {
    try {
      const data = await metricApi.uploadExcelFile(file);
      data && setMetrics(data);
      message.success(`  : ${file.name}`);
    } catch (error) {
      message.error(`   : ${file.name}`);
    }
  };

  const onFileDownload = async () => {
    metricApi
      .downloadExcelFile()
      .then((data: Blob) => {
        const url = window.URL.createObjectURL(new Blob([data]));
        const link = document.createElement("a");
        link.style.display = "none";
        link.href = url;
        link.setAttribute("download", "Metrics.xlsx");
        document.body.appendChild(link);
        link.click();
        link.remove();
      })
      .catch((error: Error) => {
        console.error(":", error);
        message.error(error.message || "     Excel");
      });
  };

  return (
    <>
      <EditableTable
        headers={GetMetricHeaders()}
        data={metrics}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
        onFileUpload={onFileUpload}
        onFileDownload={onFileDownload}
        loading={loading}
      />
    </>
  );
};
