import { message } from "antd";

import { useEffect, useState } from "react";

import { GetRightsHolderHeaders } from "./columns";
import { rightsHolderApi } from "../../api/RightsHolder";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { RightsHolder } from "../../interfaces";

export const RightsHolders = () => {
  const [rightsHolders, setRightsHolders] = useState<RightsHolder[]>([]);
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    setLoading(true);
    rightsHolderApi
      .getRightsHolders()
      .then((data) => {
        if (data) {
          setRightsHolders(data);
        }
      })
      .finally(() => setLoading(false));
  }, []);

  const onAdd = () => {
    rightsHolderApi.createRightsHolder().then((newRightsHolder) => {
      if (newRightsHolder) {
        setRightsHolders([...rightsHolders, newRightsHolder]);
      }
    });
  };

  const onUpdate = (updatedRightsHolder: RightsHolder) => {
    rightsHolderApi.updateRightsHolder(updatedRightsHolder).then(() => {
      const index = rightsHolders.findIndex(
        (cc) => cc.id === updatedRightsHolder.id
      );
      if (index !== -1) {
        const updatedDataSource = [...rightsHolders];
        updatedDataSource.splice(index, 1, updatedRightsHolder);
        setRightsHolders(updatedDataSource);
      }
    });
  };

  const onDelete = (id: number) => {
    rightsHolderApi.deleteRightsHolder({ id }).then(() => {
      const index = rightsHolders.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...rightsHolders];
        updatedDataSource.splice(index, 1);
        setRightsHolders(updatedDataSource);
      }
    });
  };

  const onFileUpload = async ({ file }: { file: File }) => {
    try {
      const data = await rightsHolderApi.uploadExcelFile(file);
      data && setRightsHolders(data);
      message.success(`  : ${file.name}`);
    } catch (error) {
      message.error(`   : ${file.name}`);
    }
  };

  const onFileDownload = async () => {
    rightsHolderApi
      .downloadExcelFile()
      .then((data: Blob) => {
        const url = window.URL.createObjectURL(new Blob([data]));
        const link = document.createElement("a");
        link.style.display = "none";
        link.href = url;
        link.setAttribute("download", "RightsHolders.xlsx");
        document.body.appendChild(link);
        link.click();
        link.remove();
      })
      .catch((error: Error) => {
        console.error(":", error);
        message.error(error.message || "     Excel");
      });
  };

  return (
    <>
      <EditableTable
        headers={GetRightsHolderHeaders()}
        data={rightsHolders}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
        onFileUpload={onFileUpload}
        onFileDownload={onFileDownload}
        loading={loading}
      />
    </>
  );
};
