/* eslint-disable @typescript-eslint/no-explicit-any */
import {
  Button,
  Table,
  Form,
  Input,
  Select,
  InputNumber,
  DatePicker,
  Upload
} from "antd";
import moment from "moment";

import { FC, useState } from "react";

// import { Spinner } from "@ois-design-ui/components";
// import {
//   Import12,
//   Plus12
// } from "@ois-design-ui/components/lib/components/icons";

import { Header, CellDataType, EditableTableProps } from "./interfaces";

export const EditableTable: FC<EditableTableProps> = ({
  headers,
  data,
  extraData,
  onUpdate,
  onAdd,
  onDelete,
  onFileUpload,
  onFileDownload,
  loading,
  error
}) => {
  const [form] = Form.useForm();
  const [editingRowId, setEditingRowId] = useState<number | null>(null);

  const columns = [
    ...headers.map((el: Header) => {
      return {
        key: el.key,
        title: el.title,
        dataIndex: el.dataIndex,
        width: el.width,
        render: (cellData: any, row: any) => {
          if (el.dataType === CellDataType.Text) {
            if (editingRowId === row.id && el.isEditable) {
              return (
                <Form.Item
                  key={el.key}
                  name={el.dataIndex}
                  rules={[
                    {
                      required: el.required,
                      message: el.message
                    }
                  ]}
                >
                  <Input />
                </Form.Item>
              );
            } else {
              return <div key={el.key}>{cellData}</div>;
            }
          } else if (el.dataType === CellDataType.Number) {
            if (editingRowId === row.id && el.isEditable) {
              return (
                <Form.Item
                  key={el.key}
                  name={el.dataIndex}
                  rules={[
                    {
                      required: el.required,
                      message: el.message
                    }
                  ]}
                >
                  <InputNumber />
                </Form.Item>
              );
            } else {
              return <div key={el.key}>{cellData}</div>;
            }
          } else if (el.dataType === CellDataType.Select) {
            if (editingRowId === row.id && el.extraDataName && el.isEditable) {
              return (
                <Form.Item
                  key={el.key}
                  name={el.dataIndex}
                  rules={[
                    {
                      required: el.required,
                      message: el.message
                    }
                  ]}
                >
                  <Select
                    options={[
                      ...extraData[el.extraDataName].map((option: any) => {
                        if (el.extraDataPropName) {
                          return {
                            value: option[el.extraDataPropName],
                            label: option[el.extraDataPropName]
                          };
                        }
                      })
                    ]}
                  />
                </Form.Item>
              );
            } else {
              return (
                <div key={el.key}>
                  {cellData && cellData[el.propertyName ?? ""]}
                </div>
              );
            }
          } else if (el.dataType === CellDataType.Date) {
            if (editingRowId === row.id && el.isEditable) {
              return (
                <Form.Item
                  key={el.key}
                  name={el.dataIndex}
                  rules={[
                    {
                      required: el.required,
                      message: el.message
                    }
                  ]}
                >
                  <DatePicker format={"DD-MM-YYYY"} />
                </Form.Item>
              );
            } else {
              return (
                <div key={el.key}>{moment(cellData).format("DD-MM-YYYY")}</div>
              );
            }
          } else if (el.dataType === CellDataType.Nested && el.parentName) {
            let currentObject = row;
            for (const key of el.parentName) {
              if (currentObject && key in currentObject) {
                currentObject = currentObject[key];
              } else {
                console.error(`Property "${key}" not found.`);
                break;
              }
            }
            return <div key={el.key}>{currentObject}</div>;
          }
        }
      };
    }),
    {
      key: "actions",
      title: "",
      width: 100,
      render: (record: EditableTableProps["data"][0]) => {
        return (
          <>
            {(editingRowId === null || editingRowId !== record.id) && (
              <>
                <Button
                  type="link"
                  onClick={() => {
                    setEditingRowId(record.id);
                    const values: any = {};
                    headers.forEach((el) => {
                      if (el.dataType === CellDataType.Date) {
                        values[el.dataIndex] = moment(record[el.dataIndex]);
                      } else if (
                        el.dataType === CellDataType.Select &&
                        record[el.dataIndex]
                      ) {
                        const innerObj = record[el.dataIndex];
                        values[el.dataIndex] = innerObj[el.propertyName ?? ""];
                      } else {
                        values[el.dataIndex] = record[el.dataIndex];
                      }
                    });
                    form.setFieldsValue(values);
                  }}
                >
                  
                </Button>
                <Button type="link" onClick={() => onDelete(record.id)}>
                  
                </Button>
              </>
            )}
            {editingRowId === record.id && (
              <>
                <Button
                  type="link"
                  onClick={() => {
                    setEditingRowId(null);
                  }}
                >
                  
                </Button>
                <Button type="link" htmlType="submit">
                  
                </Button>
              </>
            )}
          </>
        );
      }
    }
  ];

  const onEdit = (rowData: any) => {
    if (editingRowId !== null) {
      const srcRowData = data.find((el) => el.id === editingRowId);
      const updatedRowData = { ...srcRowData, ...rowData };
      headers.forEach((header) => {
        for (const key in rowData) {
          if (
            key === header.dataIndex &&
            header.dataType === CellDataType.Select &&
            rowData[key]
          ) {
            rowData[key]
              ? (updatedRowData[header.dataIndex + "Id"] = extraData[
                  header.extraDataName ?? ""
                ].find(
                  (el: any) =>
                    el[header.extraDataPropName ?? ""] === rowData[key]
                ).id)
              : (updatedRowData[header.dataIndex + "Id"] = null);
            delete updatedRowData[key];
          }
          if (
            key === header.dataIndex &&
            header.dataType === CellDataType.Date
          ) {
            updatedRowData[key] = rowData[key].format(
              "YYYY-MM-DDTHH:mm:ss.SSS[Z]"
            );
          }
        }
      });
      onUpdate(updatedRowData);
      setEditingRowId(null);
    }
  };

  if (error) return null;

  return (
    // <Spinner spinning={loading}>
    <div style={{ paddingLeft: "10px", paddingRight: "10px" }}>
      <Button
        style={{ marginBottom: "10px", marginRight: "10px" }}
        // icon={<Plus12 />}
        onClick={onAdd}
      >
         
      </Button>
      {onFileUpload && (
        <Upload
          accept=".xlsx, .xls"
          customRequest={onFileUpload}
          showUploadList={false}
        >
          <Button
            style={{ marginBottom: "10px", marginRight: "10px" }}
            // icon={<Import12 />}
          >
              Excel
          </Button>
        </Upload>
      )}
      {onFileDownload && (
        <Button
          onClick={onFileDownload}
          style={{ marginBottom: "10px" }}
          // icon={<Import12 />}
        >
            Excel
        </Button>
      )}
      <Form form={form} onFinish={(values: any) => onEdit(values)}>
        <Table
          columns={columns}
          dataSource={data.map((el) => {
            return { ...el, key: el.id };
          })}
        />
      </Form>
    </div>
    // </Spinner>
  );
};
