import { message } from "antd";

import { useEffect, useState } from "react";

import { GetProductHeaders } from "./columns";
import {
  productApi,
  softwareLifeStageApi,
  rightsHolderApi,
  registrationStatusApi,
  productFamilyApi
} from "../../api";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { Product } from "../../interfaces";

export const Products = () => {
  const [products, setProducts] = useState<Product[]>([]);
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  const [extraData, setExtraData] = useState<any>();
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    setLoading(true);
    productApi
      .getProducts()
      .then((data) => {
        if (data) {
          setProducts(data);
        }
      })
      .finally(() => setLoading(false));
    softwareLifeStageApi.getSoftwareLifeStages().then((softwareLifeStages) => {
      rightsHolderApi.getRightsHolders().then((rightsHolders) => {
        registrationStatusApi
          .getRegistrationStatuses()
          .then((registrationStatuses) => {
            productFamilyApi.getProductFamilies().then((productFamilies) => {
              setExtraData({
                softwareLifeStages,
                rightsHolders,
                registrationStatuses,
                productFamilies
              });
            });
          });
      });
    });
  }, []);

  const onAdd = () => {
    productApi.createProduct().then((newProduct) => {
      if (newProduct) {
        setProducts([...products, newProduct]);
      }
    });
  };

  const onUpdate = (updatedProduct: Product) => {
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    productApi.updateProduct(updatedProduct).then((data: any) => {
      const updatedProducts = [...products].map((el) => {
        if (el.id !== updatedProduct.id) {
          return el;
        } else {
          return data;
        }
      });
      setProducts(updatedProducts);
      // const index = products.findIndex((cc) => cc.id === updatedProduct.id);
      // if (index !== -1 && data) {
      //   const updatedDataSource = [...products];
      //   updatedDataSource.splice(index, 1, data);
      //   setProducts(updatedDataSource);
      // }
    });
  };

  const onDelete = (id: number) => {
    productApi.deleteProduct({ id }).then(() => {
      const index = products.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...products];
        updatedDataSource.splice(index, 1);
        setProducts(updatedDataSource);
      }
    });
  };

  const onFileUpload = async ({ file }: { file: File }) => {
    try {
      const data = await productApi.uploadExcelFile(file);
      data && setProducts(data);
      message.success(`  : ${file.name}`);
    } catch (error) {
      message.error(`   : ${file.name}`);
    }
  };

  const onFileDownload = async () => {
    productApi
      .downloadExcelFile()
      .then((data: Blob) => {
        const url = window.URL.createObjectURL(new Blob([data]));
        const link = document.createElement("a");
        link.style.display = "none";
        link.href = url;
        link.setAttribute("download", "Products.xlsx");
        document.body.appendChild(link);
        link.click();
        link.remove();
      })
      .catch((error: Error) => {
        console.error(":", error);
        message.error(error.message || "     Excel");
      });
  };

  return (
    <>
      <EditableTable
        headers={GetProductHeaders()}
        data={products}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
        extraData={extraData}
        onFileUpload={onFileUpload}
        onFileDownload={onFileDownload}
        loading={loading}
      />
    </>
  );
};
