import { message } from "antd";

import { useEffect, useState } from "react";

import { GetProductFamilyHeaders } from "./columns";
import { productFamilyApi } from "../../api/ProductFamily";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { ProductFamily } from "../../interfaces";

export const ProductFamilies = () => {
  const [productFamilies, setProductFamilies] = useState<ProductFamily[]>([]);
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    setLoading(true);
    productFamilyApi
      .getProductFamilies()
      .then((data) => {
        if (data) {
          setProductFamilies(data);
        }
      })
      .finally(() => setLoading(false));
  }, []);

  const onAdd = () => {
    productFamilyApi.createProductFamily().then((newProductFamily) => {
      if (newProductFamily) {
        setProductFamilies([...productFamilies, newProductFamily]);
      }
    });
  };

  const onUpdate = (updatedProductFamily: ProductFamily) => {
    productFamilyApi.updateProductFamily(updatedProductFamily).then(() => {
      const index = productFamilies.findIndex(
        (cc) => cc.id === updatedProductFamily.id
      );
      if (index !== -1) {
        const updatedDataSource = [...productFamilies];
        updatedDataSource.splice(index, 1, updatedProductFamily);
        setProductFamilies(updatedDataSource);
      }
    });
  };

  const onDelete = (id: number) => {
    productFamilyApi.deleteProductFamily({ id }).then(() => {
      const index = productFamilies.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...productFamilies];
        updatedDataSource.splice(index, 1);
        setProductFamilies(updatedDataSource);
      }
    });
  };

  const onFileUpload = async ({ file }: { file: File }) => {
    try {
      const data = await productFamilyApi.uploadExcelFile(file);
      data && setProductFamilies(data);
      message.success(`  : ${file.name}`);
    } catch (error) {
      message.error(`   : ${file.name}`);
    }
  };

  const onFileDownload = async () => {
    productFamilyApi
      .downloadExcelFile()
      .then((data: Blob) => {
        const url = window.URL.createObjectURL(new Blob([data]));
        const link = document.createElement("a");
        link.style.display = "none";
        link.href = url;
        link.setAttribute("download", "ProductFamilies.xlsx");
        document.body.appendChild(link);
        link.click();
        link.remove();
      })
      .catch((error: Error) => {
        console.error(":", error);
        message.error(error.message || "     Excel");
      });
  };

  return (
    <>
      <EditableTable
        headers={GetProductFamilyHeaders()}
        data={productFamilies}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
        onFileUpload={onFileUpload}
        onFileDownload={onFileDownload}
        loading={loading}
      />
    </>
  );
};
