import { MetricContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { Metric } from "../../interfaces";

export type { MetricContext } from "./interfaces";

export const metricApi = {
  getMetrics: async () => {
    try {
      const response = await axiosInstance.get<Metric[]>("/Metric/getMetrics");
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getMetric: async (context: MetricContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<Metric>(
        `/Metric/getMetric/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createMetric: async () => {
    try {
      const response = await axiosInstance.post<Metric>("/Metric/createMetric");
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updateMetric: async (context: Metric) => {
    try {
      await axiosInstance.put("/Metric/updateMetric", context);
    } catch (error) {
      console.error(error);
    }
  },
  deleteMetric: async (context: MetricContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(`/Metric/deleteMetric/${id}`);
    } catch (error) {
      console.error(error);
    }
  },
  uploadExcelFile: async (file: File) => {
    try {
      const formData = new FormData();
      formData.append("file", file);
      const response = await axiosInstance.post<Metric[]>(
        "/Metric/importExcel",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        }
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
      throw error;
    }
  },
  downloadExcelFile: async () => {
    try {
      const response = await axiosInstance.get("/Metric/exportExcel", {
        responseType: "blob"
      });
      return unpack(response);
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
};
