import { RegistrationStatusContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { RegistrationStatus } from "../../interfaces";

export type { RegistrationStatusContext } from "./interfaces";

export const registrationStatusApi = {
  getRegistrationStatuses: async () => {
    try {
      const response = await axiosInstance.get<RegistrationStatus[]>(
        "/RegistrationStatus/getRegistrationStatuses"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getRegistrationStatus: async (context: RegistrationStatusContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<RegistrationStatus>(
        `/RegistrationStatus/getRegistrationStatus/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createRegistrationStatus: async () => {
    try {
      const response = await axiosInstance.post<RegistrationStatus>(
        "/RegistrationStatus/createRegistrationStatus"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updateRegistrationStatus: async (context: RegistrationStatus) => {
    try {
      await axiosInstance.put(
        "/RegistrationStatus/updateRegistrationStatus",
        context
      );
    } catch (error) {
      console.error(error);
    }
  },
  deleteRegistrationStatus: async (context: RegistrationStatusContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(
        `/RegistrationStatus/deleteRegistrationStatus/${id}`
      );
    } catch (error) {
      console.error(error);
    }
  },
  uploadExcelFile: async (file: File) => {
    try {
      const formData = new FormData();
      formData.append("file", file);
      const response = await axiosInstance.post<RegistrationStatus[]>(
        "/RegistrationStatus/importExcel",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        }
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
      throw error;
    }
  },
  downloadExcelFile: async () => {
    try {
      const response = await axiosInstance.get(
        "/RegistrationStatus/exportExcel",
        {
          responseType: "blob"
        }
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
};
