import { message } from "antd";

import { useEffect, useState } from "react";

import { GetLicenseTransactionHeaders } from "./columns";
import {
  licenseTransactionApi,
  contractApi,
  priceListApi,
  licenseActionApi,
  licenseTypeApi
} from "../../api";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { LicenseTransaction } from "../../interfaces";

export const LicenseTransactions = () => {
  const [licenseTransactions, setLicenseTransactions] = useState<
    LicenseTransaction[]
  >([]);
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  const [extraData, setExtraData] = useState<any>();
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    setLoading(true);
    licenseTransactionApi
      .getLicenseTransactions()
      .then((data) => {
        if (data) {
          setLicenseTransactions(data);
        }
      })
      .finally(() => setLoading(false));
    contractApi.getContracts().then((contracts) => {
      priceListApi.getPriceLists().then((priceLists) => {
        licenseActionApi.getLicenseActions().then((licenseActions) => {
          licenseTypeApi.getLicenseTypes().then((licenseTypes) => {
            setExtraData({
              contracts,
              priceLists,
              licenseActions,
              licenseTypes
            });
          });
        });
      });
    });
  }, []);

  const onAdd = () => {
    licenseTransactionApi
      .createLicenseTransaction()
      .then((newLicenseTransaction) => {
        if (newLicenseTransaction) {
          setLicenseTransactions([
            ...licenseTransactions,
            newLicenseTransaction
          ]);
        }
      });
  };

  const onUpdate = (updatedLicenseTransaction: LicenseTransaction) => {
    licenseTransactionApi
      .updateLicenseTransaction(updatedLicenseTransaction)
      /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
      .then((data: any) => {
        const updatedLicenseTransactions = [...licenseTransactions].map(
          (el) => {
            if (el.id !== updatedLicenseTransaction.id) {
              return el;
            } else {
              return data;
            }
          }
        );
        setLicenseTransactions(updatedLicenseTransactions);
        // const index = licenseTransactions.findIndex((cc) => cc.id === updatedLicenseTransaction.id);
        // if (index !== -1 && data) {
        //   const updatedDataSource = [...licenseTransactions];
        //   updatedDataSource.splice(index, 1, data);
        //   setLicenseTransactions(updatedDataSource);
        // }
      });
  };

  const onDelete = (id: number) => {
    licenseTransactionApi.deleteLicenseTransaction({ id }).then(() => {
      const index = licenseTransactions.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...licenseTransactions];
        updatedDataSource.splice(index, 1);
        setLicenseTransactions(updatedDataSource);
      }
    });
  };

  const onFileUpload = async ({ file }: { file: File }) => {
    try {
      const data = await licenseTransactionApi.uploadExcelFile(file);
      data && setLicenseTransactions(data);
      message.success(`  : ${file.name}`);
    } catch (error) {
      message.error(`   : ${file.name}`);
    }
  };

  const onFileDownload = async () => {
    licenseTransactionApi
      .downloadExcelFile()
      .then((data: Blob) => {
        const url = window.URL.createObjectURL(new Blob([data]));
        const link = document.createElement("a");
        link.style.display = "none";
        link.href = url;
        link.setAttribute("download", "LicenseTransactions.xlsx");
        document.body.appendChild(link);
        link.click();
        link.remove();
      })
      .catch((error: Error) => {
        console.error(":", error);
        message.error(error.message || "     Excel");
      });
  };

  return (
    <>
      <EditableTable
        headers={GetLicenseTransactionHeaders()}
        data={licenseTransactions}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
        extraData={extraData}
        onFileUpload={onFileUpload}
        onFileDownload={onFileDownload}
        loading={loading}
      />
    </>
  );
};
