import { message } from "antd";

import { useEffect, useState } from "react";

import { GetPriceListHeaders } from "./columns";
import { priceListApi, metricApi, productApi } from "../../api";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { PriceList } from "../../interfaces";

export const PriceLists = () => {
  const [priceLists, setPriceLists] = useState<PriceList[]>([]);
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  const [extraData, setExtraData] = useState<any>();
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    setLoading(true);
    priceListApi
      .getPriceLists()
      .then((data) => {
        if (data) {
          setPriceLists(data);
        }
      })
      .finally(() => setLoading(false));
    metricApi.getMetrics().then((metrics) => {
      productApi.getProducts().then((products) => {
        setExtraData({
          metrics,
          products
        });
      });
    });
  }, []);

  const onAdd = () => {
    priceListApi.createPriceList().then((newPriceList) => {
      if (newPriceList) {
        setPriceLists([...priceLists, newPriceList]);
      }
    });
  };

  const onUpdate = (updatedPriceList: PriceList) => {
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    priceListApi.updatePriceList(updatedPriceList).then((data: any) => {
      const updatedPriceLists = [...priceLists].map((el) => {
        if (el.id !== updatedPriceList.id) {
          return el;
        } else {
          return data;
        }
      });
      setPriceLists(updatedPriceLists);
      // const index = priceLists.findIndex((cc) => cc.id === updatedPriceList.id);
      // if (index !== -1 && data) {
      //   const updatedDataSource = [...priceLists];
      //   updatedDataSource.splice(index, 1, data);
      //   setPriceLists(updatedDataSource);
      // }
    });
  };

  const onDelete = (id: number) => {
    priceListApi.deletePriceList({ id }).then(() => {
      const index = priceLists.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...priceLists];
        updatedDataSource.splice(index, 1);
        setPriceLists(updatedDataSource);
      }
    });
  };

  const onFileUpload = async ({ file }: { file: File }) => {
    try {
      const data = await priceListApi.uploadExcelFile(file);
      data && setPriceLists(data);
      message.success(`  : ${file.name}`);
    } catch (error) {
      message.error(`   : ${file.name}`);
    }
  };

  const onFileDownload = async () => {
    priceListApi
      .downloadExcelFile()
      .then((data: Blob) => {
        const url = window.URL.createObjectURL(new Blob([data]));
        const link = document.createElement("a");
        link.style.display = "none";
        link.href = url;
        link.setAttribute("download", "PriceLists.xlsx");
        document.body.appendChild(link);
        link.click();
        link.remove();
      })
      .catch((error: Error) => {
        console.error(":", error);
        message.error(error.message || "     Excel");
      });
  };

  return (
    <>
      <EditableTable
        headers={GetPriceListHeaders()}
        data={priceLists}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
        extraData={extraData}
        onFileUpload={onFileUpload}
        onFileDownload={onFileDownload}
        loading={loading}
      />
    </>
  );
};
