import { ProductContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { Product } from "../../interfaces";

export type { ProductContext } from "./interfaces";

export const productApi = {
  getProducts: async () => {
    try {
      const response = await axiosInstance.get<Product[]>(
        "/Product/getProducts"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getProduct: async (context: ProductContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<Product>(
        `/Product/getProduct/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createProduct: async () => {
    try {
      const response = await axiosInstance.post<Product>(
        "/Product/createProduct"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updateProduct: async (context: Product) => {
    try {
      const response = await axiosInstance.put<Product>(
        "/Product/updateProduct",
        context
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  deleteProduct: async (context: ProductContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(`/Product/deleteProduct/${id}`);
    } catch (error) {
      console.error(error);
    }
  },
  uploadExcelFile: async (file: File) => {
    try {
      const formData = new FormData();
      formData.append("file", file);
      const response = await axiosInstance.post<Product[]>(
        "/Product/importExcel",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        }
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
      throw error;
    }
  },
  downloadExcelFile: async () => {
    try {
      const response = await axiosInstance.get("/Product/exportExcel", {
        responseType: "blob"
      });
      return unpack(response);
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
};
