import { ProductFamilyContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { ProductFamily } from "../../interfaces";

export type { ProductFamilyContext } from "./interfaces";

export const productFamilyApi = {
  getProductFamilies: async () => {
    try {
      const response = await axiosInstance.get<ProductFamily[]>(
        "/ProductFamily/getProductFamilies"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getProductFamily: async (context: ProductFamilyContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<ProductFamily>(
        `/ProductFamily/getProductFamily/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createProductFamily: async () => {
    try {
      const response = await axiosInstance.post<ProductFamily>(
        "/ProductFamily/createProductFamily"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updateProductFamily: async (context: ProductFamily) => {
    try {
      await axiosInstance.put("/ProductFamily/updateProductFamily", context);
    } catch (error) {
      console.error(error);
    }
  },
  deleteProductFamily: async (context: ProductFamilyContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(`/ProductFamily/deleteProductFamily/${id}`);
    } catch (error) {
      console.error(error);
    }
  },
  uploadExcelFile: async (file: File) => {
    try {
      const formData = new FormData();
      formData.append("file", file);
      const response = await axiosInstance.post<ProductFamily[]>(
        "/ProductFamily/importExcel",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        }
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
      throw error;
    }
  },
  downloadExcelFile: async () => {
    try {
      const response = await axiosInstance.get("/ProductFamily/exportExcel", {
        responseType: "blob"
      });
      return unpack(response);
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
};
