import { ContractContext } from "./interfaces";
import axiosInstance, { unpack } from "../../infrastructure/axios-config";
import { Contract } from "../../interfaces";

export type { ContractContext } from "./interfaces";

export const contractApi = {
  getContracts: async () => {
    try {
      const response = await axiosInstance.get<Contract[]>(
        "/Contract/getContracts"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  getContract: async (context: ContractContext) => {
    try {
      const { id } = context;
      const response = await axiosInstance.get<Contract>(
        `/Contract/getContract/${id}`
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  createContract: async () => {
    try {
      const response = await axiosInstance.post<Contract>(
        "/Contract/createContract"
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  updateContract: async (context: Contract) => {
    try {
      const response = await axiosInstance.put<Contract>(
        "/Contract/updateContract",
        context
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
    }
  },
  deleteContract: async (context: ContractContext) => {
    try {
      const { id } = context;
      await axiosInstance.delete(`/Contract/deleteContract/${id}`);
    } catch (error) {
      console.error(error);
    }
  },
  uploadExcelFile: async (file: File) => {
    try {
      const formData = new FormData();
      formData.append("file", file);
      const response = await axiosInstance.post<Contract[]>(
        "/Contract/importExcel",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        }
      );
      return unpack(response);
    } catch (error) {
      console.error(error);
      throw error;
    }
  },
  downloadExcelFile: async () => {
    try {
      const response = await axiosInstance.get("/Contract/exportExcel", {
        responseType: "blob"
      });
      return unpack(response);
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
};
