/* eslint-disable @typescript-eslint/no-explicit-any */
export interface EditableTableProps {
  headers: Header[];
  data: any[];
  extraData?: any;
  onUpdate: (updatedData: any) => void;
  onAdd: () => void;
  onDelete: (id: number) => void;
  onFileUpload?: (e: any) => void;
  onFileDownload?: (e: any) => void;
  loading?: boolean;
  error?: boolean;
}

export enum CellDataType {
  Number = "number",
  Date = "date",
  Text = "text",
  Boolean = "bool",
  Select = "select",
  Nested = "nested"
}

export interface Header {
  key: string;
  dataIndex: string;
  title: string;
  isEditable: boolean;
  required: boolean;
  message?: string;
  dataType: CellDataType;
  propertyName?: string;
  extraDataName?: string;
  extraDataPropName?: string;
  parentName?: string[];
  width?: number;
}
