import { message } from "antd";

import { useEffect, useState } from "react";

import { GetLicenseTypeHeaders } from "./columns";
import { licenseTypeApi } from "../../api/LicenseType";
import { EditableTable } from "../../components/EditableTable/EditableTable";
import { LicenseType } from "../../interfaces";

export const LicenseTypes = () => {
  const [licenseTypes, setLicenseTypes] = useState<LicenseType[]>([]);
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    setLoading(true);
    licenseTypeApi
      .getLicenseTypes()
      .then((data) => {
        if (data) {
          setLicenseTypes(data);
        }
      })
      .finally(() => setLoading(false));
  }, []);

  const onAdd = () => {
    licenseTypeApi.createLicenseType().then((newLicenseType) => {
      if (newLicenseType) {
        setLicenseTypes([...licenseTypes, newLicenseType]);
      }
    });
  };

  const onUpdate = (updatedLicenseType: LicenseType) => {
    licenseTypeApi.updateLicenseType(updatedLicenseType).then(() => {
      const index = licenseTypes.findIndex(
        (cc) => cc.id === updatedLicenseType.id
      );
      if (index !== -1) {
        const updatedDataSource = [...licenseTypes];
        updatedDataSource.splice(index, 1, updatedLicenseType);
        setLicenseTypes(updatedDataSource);
      }
    });
  };

  const onDelete = (id: number) => {
    licenseTypeApi.deleteLicenseType({ id }).then(() => {
      const index = licenseTypes.findIndex((cc) => cc.id === id);
      if (index !== -1) {
        const updatedDataSource = [...licenseTypes];
        updatedDataSource.splice(index, 1);
        setLicenseTypes(updatedDataSource);
      }
    });
  };

  const onFileUpload = async ({ file }: { file: File }) => {
    try {
      const data = await licenseTypeApi.uploadExcelFile(file);
      data && setLicenseTypes(data);
      message.success(`  : ${file.name}`);
    } catch (error) {
      message.error(`   : ${file.name}`);
    }
  };

  const onFileDownload = async () => {
    licenseTypeApi
      .downloadExcelFile()
      .then((data: Blob) => {
        const url = window.URL.createObjectURL(new Blob([data]));
        const link = document.createElement("a");
        link.style.display = "none";
        link.href = url;
        link.setAttribute("download", "LicenseTypes.xlsx");
        document.body.appendChild(link);
        link.click();
        link.remove();
      })
      .catch((error: Error) => {
        console.error(":", error);
        message.error(error.message || "     Excel");
      });
  };

  return (
    <>
      <EditableTable
        headers={GetLicenseTypeHeaders()}
        data={licenseTypes}
        onAdd={onAdd}
        onUpdate={onUpdate}
        onDelete={onDelete}
        onFileUpload={onFileUpload}
        onFileDownload={onFileDownload}
        loading={loading}
      />
    </>
  );
};
